
body {
  background-color: #f0f0f0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#drum-machine {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff; 
  border-radius: 10px;
  border: 2px solid #333; 
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
  width: 80%; 
  max-width: 400px;
}

#display {
  font-size: 24px;
  margin-top: 15px;
}


.drum-pad {
  display: inline-block;
  margin: 10px;
  padding: 15px 30px;
  font-size: 18px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  outline: none;
}

.drum-pad:focus {
  outline: none; 
}

.drum-pad:hover {
  background-color: #45a049;
}

.volume-slider {
  margin-top: 20px;
  display: flex;
  align-items: center;
}

#volume {
  width: 150px;
  margin: 0 10px;
}

.volume-value {
  font-size: 14px;
}

.drum-pad-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  width: 100%;
  max-width: 400px;
  margin-top: 20px;
  text-align: center;
}

h1 {
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif ;
  font-size: 36px;
  text-align: center;
  margin-bottom: 20px;
}

#power-button {
  background-color:#45a049;
  color: white;
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

#power-button:hover {
  background-color:green;
}

#power-button.power-off {
  background-color:#2c3e50;
}

#power-button.power-off:hover {
  background-color:darkslategray
}



